import React from "react";
import {Link} from 'react-router-dom';


function Home() {
    return (
        <section className="text-gray-600 body-font">
            <div className="container mx-auto flex px-5 py-24 items-center justify-center flex-col">
                <img className="lg:w-2/6 md:w-3/6 w-5/6 mb-10 object-cover object-center rounded-3xl" alt="hero" src="https://source.unsplash.com/720x600/?meetings/"/>
                <div className="text-center lg:w-2/3 w-full">
                <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-900">Meeting Management App</h1>
                <p className="mb-8 leading-relaxed">Create and Manage Meetings</p>
                <div className="flex justify-center">
                    <Link to='/events' className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full mr-3">All Meetings &mdash;</Link>
                    <Link to='/add-event' className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded-full">Create a new Meeting &mdash;</Link>
                </div>
                </div>
            </div>
        </section>
    );
}

export default Home;